import { Block, Controller } from '@ali/4ever-cangjie';

export interface IsVoteCardEditStatusProps {
  node: Block;
}

export default function isVoteCardEditStatus(controller: Controller, { node }: IsVoteCardEditStatusProps) {
  const { injections } = controller.value;
  const findNode = injections.find(n => n.key === node.key && n.data.editing);
  return Boolean(findNode);
}

export function isVoteCardVotingStatus(controller: Controller, { node }: IsVoteCardEditStatusProps) {
  const { injections } = controller.value;
  const findNode = injections.find(n => n.key === node.key && n.data.voting);
  return Boolean(findNode);
}
